body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{background-color:#121616;height:auto}
.home-box{background:#0d1112;background:linear-gradient(to right, #0d1112 0%, #1d3130 45%, #136952 85%, #019664 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr="#0d1112", endColorstr="#198c71",GradientType=1 );height:90vh;display:flex;flex-direction:column;justify-content:space-between}.home-nav{display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:rgba(0,0,0,.295);width:100%}@media(min-width: 812px){.home-nav{flex-direction:row}}
header{margin:0 auto;padding:10px 3vw}header svg{width:60px;height:60px}
.home_link{display:flex;justify-content:center;width:150px;height:50px;font-size:150%;margin-top:22px}.home_link span{color:#019664}.home_link a{color:#343636;text-decoration:none;font-family:"Nunito";font-weight:bold;padding-top:2px}.home_link:hover span{color:#10c286}.home_link:hover a{color:#10c286;border-color:#10c286;border-bottom:none}@media(min-width: 812px){.home_link:hover a{border-bottom:solid}}

.content-type-box{height:600px}
.about-box{height:400px}
